.App {
  text-align: left;
}
.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}
.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}
.App-intro {
  font-size: large;
}
@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
body {
  color:  hsl(0, 0%, 10%);
  background-color: hsl(0, 0%, 98%);
}
table {
  position: static;
  display:  inline-block;
  border-collapse:  collapse;
  text-align: left;
}
th {
  border:   1px solid hsl(0, 0%, 80%);
  padding:  5px;
}
td {
  border:   1px solid hsl(0, 0%, 80%);
  padding:  5px;
}
td.empty {
  background-color: hsl(0, 0%, 80%);
}
div.RctHeader {
  position: fixed;
  display: block;
  top: 0px;
  width: 100%;
  height: 30px;
  z-index: 1;
  text-align: right;
  font-size: small;
  background-color: hsl(0, 0%, 94%);
}
div.RctHeader div.demo {
  position: absolute;
  display: inline-block;
  left: 2px;
  top: 2px;
}
div.RctHeader div.button {
  position: static;
  display: inline-block;
  width:30px;
  height:30px;
}
div.RctHeader div.button:hover {
  cursor: pointer;
  background-color: hsl(0, 0%, 98%);
}
div.RctMenu {
  position: absolute;
  display: inline-block;
  right: 0px;
  top: 30px;
  bottom: 0;
  z-index: 1;
  background-color: hsl(0, 0%, 98%);
}
div.RctMenu ul {
  position: fixed;
  margin:0px; /* overrides default */
  padding: 0px; /* overrides default */
  right: 0;
  top: 30px;
  bottom:0;
  list-style: none;
  overflow-y: auto; /* scrollbar hides if not needed */
}
div.RctMenu li {
  border: 1px solid hsl(0, 0%, 98%);
  background-color: hsl(0, 0%, 94%);
}
div.RctMenu li:hover {
  cursor: pointer;
  background-color: hsl(0, 0%, 98%);
}
div.RctNavMenu {
  position: absolute;
  display: inline-block;
  right: 0px;
  top: 30px;
  bottom: 0;
  z-index: 1;
  background-color: hsl(0, 0%, 98%);
}
div.RctNavMenu ul {
  position: fixed;
  margin:0px; /* overrides default */
  padding: 0px; /* overrides default */
  right: 0;
  top: 30px;
  bottom:0;
  list-style: none;
  overflow-y: auto; /* scrollbar hides if not needed */
}
div.RctNavMenu li {
  border: 1px solid hsl(0, 0%, 98%);
  background-color: hsl(0, 0%, 94%);
}
div.RctNavMenu li:hover {
  cursor: pointer;
  background-color: hsl(0, 0%, 98%);
}
div.RctNavMenu div.currentLevelCaption {
  font-size: x-small;
  font-weight: bold;
  background-color: white;
}
li.RctButton {
  height: 30px;
}
div.RctViewport {
  position: absolute;
  display: block;
  top: 30px;
  width: 100%;
  word-wrap: break-word;
  text-align: center;
  background-color: hsl(0, 0%, 98%);
}
div.RctViewport div.subViewport {
  position: static;
  display: inline-block;
  z-index: 0;
  text-align: left;
  background-color: hsl(0, 0%, 98%);
}
span.mjx-chtml.MJXc-display {
  margin-top: 6px;    /* overrides ~15px default */
  margin-bottom: 6px; /* overrides ~15px default */
  display: inline-block;
}
span.bold {
  font-weight: bold;
}


